.App {
  text-align: center;
  max-width: 10000px;
  min-width: 500px;
  max-height: 4000px;
  min-height: 100px;
}

a {
  text-decoration: none;
  color: black;
}

.ropdown-menu {
  border-color: rgb(160, 16, 16);
}

.top-items {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  margin-left: -8%;
  height: 55px;
}

.App-logo {
  width: 145px;
}

.dropdown-menu {
  border-style: none;
  background-color: hsl(30, 100%, 99%);
  transform: scale(0.1);
  height: 260px;
  border-radius: 10px;
  text-align: left;
  padding-left: 10px;
}

.dropdown-menu li {
  margin-top: 10px;
  border-style: ridge;
}

.items-menu {
  height: 100%;
  display: inline-block;
}

main {
  background-color: rgb(26, 132, 63);
  height: 265px;
  border-radius: 0px 0px 250px 900px;
}

.content-items {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
}

.items-left {
  display: flex;
  flex-direction: column;
  color: white;
  width: 50%;
  text-align: left;
  justify-content: space-between;
  margin-top: 2%;
}

.p {
  width: 50%;
  font-size: 17px;
  color: white !important;
}

h4 {
  font-size: 27px;
  font-weight: bold;
}

.img-left {
  width: 30%;
  left: auto;
  margin-top: 10px;
}

.items-right {
  background-color: rgb(255, 255, 255);
  width: 23%;
  align-items: flex-end;
  margin-left: 50%;
  margin-top: -39vh;
  height: 150%;
  border-radius: 5px;
  box-shadow: 0px 0px 20px 1px rgba(0, 0, 0, 0.3);
  padding-top: 10px;
  color: rgb(124, 124, 124);
  font-weight: bold;
  font-size: 15px;
}

.right-items {
  display: flex;
  flex-direction: column;
  align-items: center;
}

select {
  border-radius: 5px;
  padding-left: 5px;
  height: 40px;
  margin-top: 15px;
  padding-right: 110px;
}

.brand {
  width: 90%;
  display: flex;
  flex-direction: column;
  margin-bottom: 40px;
  margin-top: -20px;
}

.agendar-item {
  background-color: rgb(245, 99, 0);
  height: 40px;
  box-shadow: 0 4px 8px rgb(176 49 0 / 25%);
  border-radius: 4px;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  color: white;
  margin-top: 20px;
  text-decoration: none;
  border: none;
}

.agendar-item:hover {
  color: white
}

.footer-form {
  display: flex;
  flex-direction: row;
}

.venda {
  margin-left: -5px;
  text-align: left;
  padding-left: 40px;
  align-items: center;
  justify-content: flex-start;
  display: flex;
  margin-top: 20px;
  font-size: 14px;
  background-image: url('./shield.png');
  background-repeat: no-repeat;
  background-position: left;
  background-position-x: 10px;
  background-size: 25px;
  height: 30px;
}

.atendimento {
  margin-left: -9px;
  text-align: left;
  padding-left: 40px;
  align-items: center;
  justify-content: flex-start;
  display: flex;
  margin-top: 20px;
  font-size: 14px;
  background-image: url('./star.png');
  background-repeat: no-repeat;
  background-position: left;
  background-position-x: 15px;
  background-position-y: 0px;
  background-size: 25px;
  height: 30px;
}

.content-main h3 {
  margin-top: 4%;
}

.content-image {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  color: rgb(94, 92, 92);
  text-align: left;
  margin-top: 3%;
  margin-right: 10%;
  font-size: 15px;
}

h5, h3 {
  color: rgb(78, 77, 77);
}

.text-image {
  color: rgb(155, 153, 153);
}

.imagem1 {
  display: flex;
  flex-direction: column;
  width: 20%;
  margin-right: 5%;
}

.imagem2 {
  display: flex;
  flex-direction: column;
  width: 20%;
  margin-right: 5%;
}

.imagem3 {
  display: flex;
  flex-direction: column;
  width: 17%;
}

.main3 {
  background-color: rgb(250, 250, 250);
}

.main-items3 {
  color: rgb(165, 164, 164);
}

h3, h5 {
  margin-top: 3%;
  color: rgb(114, 112, 112);
  font-size: 22px;
}

.main-items3 {
  display: flex;
  flex-direction: column;
  margin-top: 5%;
}

.all-image {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.main3-img {
  width: 65px;
}

.m3 {
  width: 15%;
  margin-left: 25px;
  text-align: left;
}

.venda-rapida-seguro {
  align-self: flex-start;
  margin-top: 90px;
}

.duvida {
  color: RGB(174, 147, 196);
  background-color: rgba(161, 21, 21, 0, 1);
  border-style: solid;
  border-color: rgb( 182, 207, 214);
  padding: 10px;
  width: 16.5%;
  align-self: center;
  margin-bottom: 10px;
  color: rgb(87, 18, 107);
  margin-top: 19px;
  font-style: normal;
  transition: 0.5s;
  cursor: default;
  cursor: pointer
}

.duvida:hover {
  background-color: rgb(214, 228, 238);
  color: rgb(42, 113, 156)
}

@media(max-width: 1400px) {
  .duvida {
    width: 23%;
  }
  .items-right {
    width: 30%;
  }
  .pioneiro {
    margin-left: 10px;
    font-size: 15px;
  }
  .h3-main3 {
    font-size: 20px;
  }
}

.end-items-main3 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  justify-self: center;
  margin-top: 8%;
}

.img-car {
  width: 70%;
  float: right;
}

.text-main3 {
  text-align: left;
  width: 65%;
  margin-left: 3%;
}

.pioneiro {
  display: flex;
  justify-content: space-around;
  margin-right: 30%;
  margin-top: 5%;
  align-items: center;
  color: rgb(146, 145, 145);
}

.h3-main3 {
  color: rgb( 27, 132, 71);
  font-size: 25px;
}

.title {
  font-size: 25px;
  margin-top: 5%;
}

.reclame {
  cursor: pointer;
}

.carousel-fade{ 
  background: rgb(54, 52, 52);
  color: black;
}

.teste{ 
display: flex;
flex-direction: row;
align-items: center;
justify-content: center;
}

.img-format{
  border-style: outset;
  margin-left: 10%;
}

